<template>
  <div>
    <header class="per_center mb0">
      <div class="pre_title">
        <router-link class="icon-ArtboardCopy" to="/index"></router-link>
      </div>
      <h3>酒店图片</h3>
    </header>
    <section>
      <div class="list">
        <div class="titleimg">
          <a :class="isInx==idx?'c_009af0':''" v-for="(item,idx) in imgType" @click="isInx=idx">{{item}}</a>
          <i class="jpIndex_line"></i>
        </div>
        <div style="height: 0.5rem;"></div>
        <div class="img-list">
          <!--全部-->
          <ul class="div1" v-show="isInx==0">
            <li @click="openPreImg(allimglist,idx)" v-for="(item,idx) in allimglist">
              <img :src="item.smallSizeUrl" alt="">
            </li>
          </ul>
          <!--外觀-->
          <ul class="div1" v-show="isInx==1">
            <li @click="openPreImg(imgList.hotelExteriorImgs,idx)"
                v-for="(item,idx) in imgList.hotelExteriorImgs">
              <img :src="item.smallSizeUrl" alt="">
            </li>
          </ul>
          <!--內景-->
          <ul class="div1" v-show="isInx==2">
            <li @click="openPreImg(imgList.hotelInteriorImgs,idx)" v-for="(item,idx) in imgList.hotelInteriorImgs">
              <img :src="item.smallSizeUrl" alt="">
            </li>
          </ul>
          <!--房型-->
          <ul class="div1" v-show="isInx==3">
            <li @click="openPreImg(imgList.roomsImgs,idx)" v-for="(item,idx) in imgList.roomsImgs">
              <img :src="item.smallSizeUrl" alt="">
            </li>
          </ul>
          <!--設施-->
          <ul class="div1" v-show="isInx==4">
            <li @click="openPreImg(imgList.hotelFacilitiesImgs,idx)" v-for="(item,idx) in imgList.hotelFacilitiesImgs">
              <img :src="item.smallSizeUrl" alt="">
            </li>
          </ul>
          <!--其他-->
          <ul class="div1" v-show="isInx==5">
            <li @click="openPreImg(imgList.othersImgs,idx)" v-for="(item,idx) in imgList.othersImgs">
              <img :src="item.smallSizeUrl" alt="">
            </li>
          </ul>
          <div v-show="isSpaceImg" class="space-notice"><i class="icon-Nopicture"></i>
            <p>暂无{{imgType[isInx]}}图片</p>
          </div>
        </div>
      </div>
    </section>

    <!--img 预览-->
    <transition name="slideUD">
      <div class="preview-img" v-if="isPreImg">
        <header class="per_center mb0">
          <div class="pre_title">
            <span class="icon-ArtboardCopy" @click="isPreImg=false"></span>
            <!--<router-link tag="span" class="icon-ArtboardCopy" to="index"></router-link>-->
          </div>
        </header>
        <section>
          <div class="swiper-container">
            <div class="swiper-wrapper">
              <template>
                <img :data-title="roomType[item.imageType-1]" :data-room="item.title" v-for="(item,idx) in preImgList" :src="item.midSizeUrl"
                     alt="" class="swiper-slide">
              </template>
            </div>
          </div>
        </section>
        <footer>
          <span class="img-title-l">
            <p class="preImgSubtitle limit-word">房型说明</p>
            <p class="preImgtitle">全部</p>
          </span>
          <span to="/index" class="all-img" @click="isPreImg=false"><i class="icon-Artboard2"></i></span>
          <span class="img-num"><i><em class="preimgidx"></em>/{{preImgList.length}}</i></span>
        </footer>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        isInx: '',
        isPreImg: false,
        isSpaceImg: false,
        imgType: ['全部', '外观', '内景', '房型', '设施', '其他'],
        roomType: ['房型', '外观', '大堂', '设施', '其他'],
        imgList: '',
        preImgList: [],
        allimglist: [],
      }
    },
    filters: {
      filtersDome(date){

      },
    },
    watch: {
      isInx(val){
        var _t = this;
        var len;
        switch (val) {
          case 0:
            len = _t.allimglist.length;
            break;
          case 1:
            len = _t.imgList.hotelExteriorImgs.length;
            break;
          case 2:
            len = _t.imgList.hotelInteriorImgs.length;
            break;
          case 3:
            len = _t.imgList.roomsImgs.length;
            break;
          case 4:
            len = _t.imgList.hotelFacilitiesImgs.length;
            break;
          case 5:
            len = _t.imgList.othersImgs.length;
            break;
        }
        len == 0 ? _t.isSpaceImg = true : _t.isSpaceImg = false;
      }
    },
    created: function () {
      this.QimgList()
    },
    mounted: function () {
      setTimeout(function () {
        var swiper = new Swiper('.swiper-container', {
          paginationClickable: true,
          spaceBetween: 30,
        });
      }, 200)

      $(".titleimg a").click(function () {
        var _width = $(this).width();
        var _index = $(this).index();
        var w = $(window).width();
        var lefts = $(this).offset().left - w * 0.05;
        $(this).parent().find(".jpIndex_line").animate({"left": lefts, "width": _width}, 200);
      })
    },
    methods: {
      /*req-res*/
      QimgList(){
        var _t = this;
        this.$ajax.post('/hotel/commodity/queryImageList.shtml').then(function (res) {
          if (res.data.result == 1) {
            _t.imgList = res.data.model
            _t.allimglist = _t.allimglist.concat(_t.imgList.hotelExteriorImgs)
            _t.allimglist = _t.allimglist.concat(_t.imgList.hotelInteriorImgs)
            _t.allimglist = _t.allimglist.concat(_t.imgList.roomsImgs)
            _t.allimglist = _t.allimglist.concat(_t.imgList.hotelFacilitiesImgs)
            _t.allimglist = _t.allimglist.concat(_t.imgList.othersImgs)
            _t.isInx = 0;
          }
        }).catch(function (err) {

        });
      },

      openPreImg(item, idx){
        //console.log(item);
        var _t = this;
        this.preImgList = item;
        this.isPreImg = true;
        setTimeout(function () {
          var swiper = new Swiper('.swiper-container', {
            spaceBetween: 10,
            loop: true,
            onInit: function (swiper) {
              swiper.slideTo(idx + 1, 0)
              var _this = $('.swiper-slide-active')
              $('.preImgtitle').html(_this.attr('data-title'));
              if(_this.attr('data-title')!='房型'){
                $('.preImgSubtitle').hide()
              }else{
                $('.preImgSubtitle').show()
              }
              $('.preImgSubtitle').html(_this.attr('data-room'));
              $('.preimgidx').html(swiper.realIndex + 1);
            },
            onSlideChangeEnd: function (swiper) {
              var _this = $('.swiper-slide-active')
              if(_this.attr('data-title')!='房型'){
                $('.preImgSubtitle').hide()
              }else{
                $('.preImgSubtitle').show()
              }
              $('.preImgtitle').html(_this.attr('data-title'));
              $('.preImgSubtitle').html(_this.attr('data-room'));
              $('.preimgidx').html(swiper.realIndex + 1);
            }
          });
        }, 200)
      }
    }
  }
</script>

<style scoped>
  .img-title-l{
    position: relative;
    width: 1.5rem;
  }
  .preImgtitle{
    font-size: 0.288rem;
    font-weight: 500;
  }
  .img-title-l .preImgSubtitle{
    width: 2.8rem;
    position: absolute;
    top: -0.45rem;
    left: 0;
    text-align: left;
    font-size: 0.4rem;
  }

</style>



